import React, { Component } from "react";
import { Form, Button, FormGroup } from "semantic-ui-react";

export default class UserProfileEditForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    debugger;
    newFirstName = e.target.firstName.value;
    newLastName = e.target.lastName.value;
    newEmail = e.target.email.value;
    newPassword = e.target.password.value;
    newPasswordConfirmation = e.target.passwordConfirmation.value;
  };

  render() {
    return (
      <Form onSubmit={this.handleSubmit}>
        <Form.Group widths="equal">
          <Form.Field>
            <label>First Name</label>
            <input
              name="firstName"
              placeholder={this.props.userData.first_name}
            />
          </Form.Field>
          <Form.Field>
            <label>Last Name</label>
            <input
              name="lastName"
              placeholder={this.props.userData.last_name}
            />
          </Form.Field>
        </Form.Group>
        <Form.Field>
          <label>Email</label>
          <input name="email" placeholder={this.props.userData.email} />
        </Form.Field>
        <Form.Group widths="equal">
          <Form.Field>
            <label>New Password</label>
            <input name="password" type="password" />
          </Form.Field>
          <Form.Field>
            <label>New Password Confirmation</label>
            <input name="passwordConfirmation" type="password" />
          </Form.Field>
        </Form.Group>
        <Button type="submit">Edit Profile</Button>
      </Form>
    );
  }
}
